.checkout-container-section {
	width: 75%;
	display: flex;
	align-self: center;
	margin-top: 3%;
	gap: 5%;
}

.checkout-total-section {
	background-color: #d9d9d9;
	display: flex;
	flex-direction: column;
	gap: 30px;
	align-self: flex-start;
	padding: 2%;
	border-radius: 4px;
	font-size: 1.4rem;
}

.checkout-total-section > h3 {
	border-bottom: 1px solid black;
}

.checkout-total-values-section {
	display: flex;
	gap: 20px;
}

.checkout-total-headers {
	display: grid;
	gap: 5px;
}

.checkout-total-values {
	display: grid;
	gap: 5px;
	font-size: 0.8rem;
}

.checkout-button {
	width: 75%;
	align-self: center;
	border: none;
	border-radius: 8px;
	background-color: #077512;
	padding: 3%;
	color: white;
}

.checkout-button:hover {
	background-color: #05610f;
	cursor: pointer;
}

.checkout-button:active {
	transform: scale(0.95);
	background-color: #044c0d;
}

.checkout-items-section {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	gap: 20px;
}

.checkout-item {
	display: grid;
	grid-template-columns: 200px repeat(3, 1fr);
	padding-bottom: 5px;
	border-bottom: 2px solid rgba(0, 0, 0, 0.3);
	gap: 2%;
}

.checkout-item > img {
	width: 100%;
	height: auto;
}

.checkout-item input {
	width: 25%;
}

.checkout-item p {
	font-size: 0.7rem;
}

.item-quantity-each,
.item-total {
	display: flex;
	flex-direction: column;
	gap: 5%;
}

.item-quantity-each p,
.item-total p {
	font-size: 0.8rem;
}
