.shop-content {
	padding-top: 50px;
	height: 100%;
	width: 85%;
	align-self: center;
	display: flex;
	align-items: flex-start;
}

.category {
	display: flex;
	font-size: 0.6rem;
	padding: 15px 0;
	border-bottom: 1px solid rgba(0, 0, 0, 0.5);
	gap: 8px;
}

.category > h1 {
	align-self: flex-end;
}

.category-children {
	display: flex;
	flex-direction: column;
	font-size: 0.6rem;
	padding: 15px 0;
	border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}

.expand-section {
	display: flex;
	gap: 8px;
}

.child-category {
	display: flex;
	gap: 8px;
	font-size: 1rem;
	margin-left: 30px;
	border-bottom: 1px solid rgba(0, 0, 0, 0.5);
	padding: 10px 0;
}

.child-category:nth-child(2) {
	padding-top: 20px;
}

.child-category:last-child {
	border: none;
}

.child-category > h5 {
	align-self: flex-end;
}
